ul {
    list-style: none;
}
.title {
    text-align: center;
}

.list {
    width: 500px;
    margin-left: auto;
    margin-right: auto;
    padding: 0px;


}

.item {
    display: flex;
    align-items: center;
}

.button {
    margin-left: 20px;
    width: 90px;
    height: 25px;
    border: 0px;
    cursor: pointer;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

.button:hover,
.button:focus {
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
border-radius: 5px;
}